import React from 'react';

const Tecnologias = () => {

const list = [
   
      {
        img: require('../assets/images/1.png')
      },
      {
        img: require("../assets/images/2.svg")
      }      , 
      {
        img: require('../assets/images/3.png')
      },
      {
        img: require('../assets/images/4.png')
      },
]

console.log(list[1].img.default)
    return ( 
    <section className="about" style={{marginTop:"150px"}}>
    <h2>Tecnologias</h2>
    <div className="divider"></div>
    <div className="about-content"  data-aos="fade-up" data-aos-duration="2000">
    {list.map((item, index) => (
      <Imagenes imagen={item.img.default} key={index} alt={index}/>
    ))}
    </div>
  </section> );
}
 
export default Tecnologias;

const Imagenes = ({imagen,alt}) => {
    return ( 
<div class="about-info" >
    <img src={imagen} alt={alt} style={{width:'50px',height:'50px'}}/>
  </div> );
}
 
